<template>
    <!-- 我的指标 -->
    <div class="sales-content" ref="wrapHeight">
        <div class="left-div">
            <div class="left-I">
                <div>
                    <div class="num-div" ref="numHeight">
                        <div>
                            <p>{{curAmounts}}</p>
                            <p>当月销售收入(元)</p>
                            <p>{{incomerate}}%</p>
                            <p>同比变化</p>
                        </div>
                        <div>
                            <p>{{curcost}}</p>
                            <p>当月销售成本(元)</p>
                            <p>{{incocurcost}}%</p>
                            <p>同比变化</p>
                        </div>
                        <div>
                            <p>{{curprofit}}</p>
                            <p>当月销售利润(元)</p>
                            <p>{{incoprofit}}%</p>
                            <p>同比变化</p>
                        </div>
                    </div>
                    <div class="tu-div">
                        <div class="chart-div" ref="myChart" :style = "{width:wrapWidth+'px',height:chartHeight+'px'}"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    const echarts = require('echarts/lib/echarts');
    require('echarts/lib/chart/line');
    require('echarts/lib/component/tooltip');
    require('echarts/lib/component/legend');
    export default {
        data() {
            return {
                monthData:[],
                amountsData:[], //销售额
                profitamountsData:[], //利润
                costamountsData:[], //成本
                curAmounts:0,
                incomerate:0,
                curcost:0,           //成本
                incocurcost:0,       //成本同比
                curprofit:0,        //利润
                incoprofit:0,       //利润同比
                wrapHeight: 189,
                wrapWidth: 793,
                chartHeight: 177,
            }
        },
        activated(){
            this.init();
        },
        mounted() {
            this.init();
            this.getDivHeight();
        },
        methods: {
            init() {
                this.getlist();
            },
            getDivHeight() {
                const numHeight = this.$refs.numHeight.offsetHeight;
                this.wrapHeight = this.$refs.wrapHeight.offsetHeight;
                this.wrapWidth = this.$refs.wrapHeight.offsetWidth;
                this.chartHeight = this.wrapHeight - numHeight;
            },

            //获取数据
            getlist(){
                let arr = [];
                let arrI = [];
                let arrII = [];
                let weekDatas = [];
                weekDatas = this.returnWeek('');
                this.monthData = [];
                this.amountsData = [];
                this.profitamountsData = [];
                this.costamountsData = [];
                this.fetch('/mktmfc/forms/getcountanalysis', {})
                .then((result)=>{
                    if(result.status === 200){
                        this.curAmounts = this.returnMoneyType(result.data.curAmounts[0]);
                        this.curcost = this.returnMoneyType(result.data.curcost[0]);
                        this.curprofit = this.returnMoneyType(result.data.curprofit[0]);
                        //销售同比
                        if(result.data.curAmounts_LastY[0] === 0){
                            this.incomerate = (result.data.curAmounts[0]/100).toFixed(2);
                        }else {
                            if(((result.data.curAmounts[0]-result.data.curAmounts_LastY[0])/result.data.curAmounts_LastY[0]) >=1){
                                this.incomerate = Math.floor((result.data.curAmounts[0]-result.data.curAmounts_LastY[0])/result.data.curAmounts_LastY[0]*10000)/100
                            }else {
                                this.incomerate = Math.floor((result.data.curAmounts[0]-result.data.curAmounts_LastY[0])/result.data.curAmounts_LastY[0]*100)
                            }
                        }
                        //成本同比
                        if(result.data.curcurcost_LastY[0] === 0){
                            this.incocurcost = (result.data.curcost[0]/100).toFixed(2);
                        }else {
                            if(((result.data.curcost[0]-result.data.curcurcost_LastY[0])/result.data.curcurcost_LastY[0]) >=1){
                                this.incocurcost = Math.floor((result.data.curcost[0]-result.data.curcurcost_LastY[0])/result.data.curcurcost_LastY[0]*10000)/100
                            }else {
                                this.incocurcost = Math.floor((result.data.curcost[0]-result.data.curcurcost_LastY[0])/result.data.curcurcost_LastY[0]*100)
                            }
                        }
                        //利润同比
                        if(result.data.curprofit_LastY[0] === 0){
                            this.incoprofit = (result.data.curprofit[0]/100).toFixed(2);
                        }else {
                            if(((result.data.curprofit[0]-result.data.curprofit_LastY[0])/result.data.curprofit_LastY[0]) >=1){
                                this.incoprofit = Math.floor((result.data.curprofit[0]-result.data.curprofit_LastY[0])/result.data.curprofit_LastY[0]*10000)/100
                            }else {
                                this.incoprofit = Math.floor((result.data.curprofit[0]-result.data.curprofit_LastY[0])/result.data.curprofit_LastY[0]*100)
                            }
                        }
                        arr = this.returnReconstruction(result.data.monthlist,result.data.amountslist,weekDatas);
                        arrI = this.returnReconstruction(result.data.monthlist,result.data.profitamounts,weekDatas);
                        arrII = this.returnReconstruction(result.data.monthlist,result.data.costamounts,weekDatas);
                        this.monthData = arr[0];
                        this.amountsData = arr[1].map(item => {
                                            return (item/10000).toFixed(2)
                                        });
                        this.profitamountsData = arrI[1].map(item => {
                                            return (item/10000).toFixed(2)
                                        });
                        this.costamountsData = arrII[1].map(item => {
                                            return (item/10000).toFixed(2)
                                        });
                    }
                })
                .then(()=>{
                    this.initEchart();
                    this.setEchart(this.monthData,this.amountsData,this.costamountsData,this.profitamountsData);
                })
               

            },
            //echart
            initEchart(){
                this.myChart = echarts.init(this.$refs.myChart);
            },

            //传参
            setEchart(monthData,amountsData,costamountsData,profitamountsData) {
                this.myChart.setOption({
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        icon: 'circle',
                        right: 2,
                        top: 65,
                        orient: 'vertical',
                        data: ['近12月销售收入走势','近12月销售成本走势','近12月销售利润走势']
                    },
                    grid: {
                        //z:10,
                        left: '9%',
                        width: '72%',
                        height: '60%',
                        bottom: '20%',
                    },
                    xAxis:  {
                        type: 'category',
                        boundaryGap: false,
                        data: monthData
                    },
                    yAxis: {
                        type: 'value',
                        name: '万(元)',
                        axisLine: {
                            show: false
                        },
                        boundaryGap: [0, '100%'],
                        axisLabel: {
                            // formatter: '{value} °C'
                        }
                    },
                    series: [
                        {
                            name:'近12月销售收入走势',
                            type:'line',
                            itemStyle: {
                                normal: {
                                    color: '#f6bfbc',
                                }
                            },
                            lineStyle: {
                                normal: {
                                    width: 3,
                                    shadowColor: 'rgba(0,0,0,0.4)',
                                    shadowBlur: 10,
                                    shadowOffsetY: 10
                                }
                            },
                            data:amountsData,
                            symbolSize: 5
                        },
                        {
                            name:'近12月销售成本走势',
                            type:'line',
                            itemStyle: {
                                normal: {
                                    color: '#38b48b'
                                }
                            },
                            lineStyle: {
                                normal: {
                                    width: 3,
                                    shadowColor: 'rgba(0,0,0,0.4)',
                                    shadowBlur: 10,
                                    shadowOffsetY: 10
                                }
                            },
                            data:costamountsData,
                            symbolSize: 5
                        },
                        {
                            name:'近12月销售利润走势',
                            type:'line',
                            itemStyle: {
                                normal: {
                                    color: '#e6b422'
                                }
                            },
                            lineStyle: {
                                normal: {
                                    width: 3,
                                    shadowColor: 'rgba(0,0,0,0.4)',
                                    shadowBlur: 10,
                                    shadowOffsetY: 10
                                }
                            },
                            data:profitamountsData,
                            symbolSize: 5
                        }
                    ]
                });
            }
        }
    }
</script>

<style lang="less" scoped type="text/less">
    .sales-content {
        display: flex;
        position: relative;
        height: 100%;
        border-radius: 4px;
        .left-div {
            display: flex;
            flex-direction: column;
            flex: 1;
            margin-right: 30px;
            width: 100%;
            .left-I {
                display: flex;
                flex-direction: column;
                flex: 1;
                /*margin-bottom: 20px;*/
                justify-content: center;
                .tu-div{
                    width: 100%;
                }
                .num-div {
                    display: flex;
                    flex: 1;
                    /*padding-top: 10px;*/
                    align-items: center;
                    div {
                        flex: 1;
                        display: flex;
                        flex-direction: column;
                        text-align: center;
                        p {
                            color: #aaa;
                            line-height: 1;
                            margin: 3px auto;
                        }
                        p:nth-child(1) {
                            color: #008AE0;
                            font-size: 20px;
                            font-weight: bold;
                        }
                        p:nth-child(3) {
                            color: #2d8cf0;
                        }
                    }
                }
                .chart-div {
                    /*width: 1000px;*/
                    /*height: 210px;*/
                    
                }
            }
        }
    }
</style>


